@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.link {
    text-align: center;
    height: 100vh;
}

.link a {
    color: #0084a8;
    font-size: 2.5em;
    text-decoration: none;
    font-weight: 500;
    margin: 2em 1em;
    display: inline-block;
    font-weight: 600;
    transition: color 166ms ease-in;
}

.link a:hover {
    color: #04C4FB;
}

.social-icon-link {
    fill: #0084a8;
}

.social-icon-link :hover {
    fill: #04C4FB;
    transition: fill 166ms ease-in;
}

strong {
    font-weight: 600;
}

#about-me-title {
    margin-left: 1em;
    text-align: left;
}

#about-me {
    -webkit-clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
    content: '';
    background-color: whitesmoke;
    width: 75%;
    display: inline-block;
    margin-top: 1em;
    margin-bottom: 1em;
}

#about-me-content {
    text-align: left;
    width: 90%;
    padding: 1em;
}

#about-me-pixel {
    width: 25%;
    display: inline-block;
    margin-top: 1em;
    margin-bottom: 1em;
}

#about-me-description {
    margin-bottom: 3em;
}

#proficiencies-title {
    display: block;
    font-weight: 600;
    text-align: center;
    margin-bottom: 1em;
}

#proficiencies ul {
    display: grid;
    grid-gap: 1rem;
    gap: 1rem;
    grid-template-columns: repeat(7, minmax(0, 1fr));
    padding: .2em;
    margin: 0em .1em 3em;
    text-align: center;
    list-style: none;
}

#proficiencies li {
    font-weight: 400;
}

#proficiencies li:hover {
    transition: font-weight 166ms ease-in-out;
    font-weight: 700;
}

#socials {
    text-align: center;
}

#socials-title {
    display: block;
    margin-bottom: 1em;
    font-weight: 600;
}

#socials a {
    display: inline-block;
    margin: .5em;
}

.show {
    background-color: black;
}
#portfolio-title {
    margin-right: 1em;
    text-align: right;
}


#portfolio {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
    content: '';
    background-color: whitesmoke;
    width: 75%;
    font-size: 1em;
    float: right;
    margin-top: 1em;
    margin-bottom: 1em;
}

#portfolio-content {
    text-align: right;
    float: right;
    width: 90%;
    padding: 1em;
}

#portfolio-pixel {
    width: 25%;
    background-color: white;
    margin-top: 1em;
    margin-bottom: 1em;
}

#about-this-title {
    margin-left: 1em;
    text-align: left;
    clear: both;
}

#about-this {
    -webkit-clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 95% 100%, 0 100%);
    content: '';
    background-color: whitesmoke;
    width: 75%;
    display: inline-block;
    margin-top: 1em;
    margin-bottom: 1em;
}

#about-this-content {
    text-align: left;
    width: 90%;
    padding: 1em;

}

#about-this-pixel {
    width: 25%;
    display: inline-block;
    margin-top: 1em;
    margin-bottom: 1em;
}

#flaticon {
    font-size: .6em;
}

.header {
    position: fixed;
    top: 50vh;
    left: 0px;
    overflow: hidden;
    margin-top: 1em;
    margin-left: 1em;
    z-index: 2;
    color: #04C4FB;
    background-color: rgba(0, 0, 0, 0);
    transition: left 800ms cubic-bezier(0.6, -0.28, .74, .05),
         color 720ms cubic-bezier(0.6, -0.28, .74, .05);
}

span {
    display: block;
}

.text {
    font-size: 2.2em;
    font-weight: 300;
    line-height: 1em;
}

.hide-header {
    left: -310px;
    color: #1e1e1e;
}
.crop-circle {
    display: block;
    position: fixed;
    width: 400px;
    height: 400px;
    overflow: hidden;
    border-radius: 50%;
    border: #04C4FB solid 4px;
    position: fixed;
    top: 50vh;
    left: 0px;
    overflow: hidden;
    z-index: 2;
    left: 70vw;
    transition: left 800ms cubic-bezier(0.6, -0.28, .74, .05), 
        border 800ms cubic-bezier(0.6, -0.28, .74, .05);
}

img {
    width: auto;
    height: 100%;
    margin-left: -50px;
}

.hide-photo {
    left: 105vw;
    border: #1e1e1e solid 4px;
}
html {
    scroll-behavior: smooth;
}

body {
    font-size: 1.5em;
    font-family: 'Poppins', sans-serif;
    background-color: #1e1e1e;
    overflow-x: hidden;
}

.title {
    color: #04C4FB;
    font-size: 2em;
    font-weight: 400;
}

.content {
    background-color: #fefefa;
}
