.link {
    text-align: center;
    height: 100vh;
}

.link a {
    color: #0084a8;
    font-size: 2.5em;
    text-decoration: none;
    font-weight: 500;
    margin: 2em 1em;
    display: inline-block;
    font-weight: 600;
    transition: color 166ms ease-in;
}

.link a:hover {
    color: #04C4FB;
}
